.proofreading-section .main-content {
  color: #ffffff;
  background: url("../../../../assets/images/proofreading_section_background.jpg") 50% 50% no-repeat fixed;
  background-size: cover;
  position: relative; }
  .proofreading-section .main-content .inner {
    padding: 60px 0;
    background: rgba(11, 25, 45, 0.92); }
  .proofreading-section .main-content h3 {
    margin-bottom: 10px; }

.proofreading-section .featureList .item {
  margin-bottom: 30px;
  position: relative;
  padding-left: 80px; }
  .proofreading-section .featureList .item:last-child {
    margin-bottom: 0; }
    .proofreading-section .featureList .item:last-child p {
      margin-bottom: 0; }
  .proofreading-section .featureList .item .icon {
    width: 50px;
    height: 50px;
    position: absolute;
    left: 0;
    top: 0;
    background: #ffa726;
    color: #ffffff;
    line-height: 50px;
    text-align: center;
    font-size: 20px;
    border-radius: 25px; }

.proofreading-section .featureList .sideInfo {
  padding: 40px;
  position: relative;
  margin-top: -110px;
  background: #ffa726;
  color: #0b192d; }

.proofreading-section .featureList .sidePhoto {
  position: relative;
  text-align: center; }
  .proofreading-section .featureList .sidePhoto img {
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    height: auto; }

@media (max-width: 767px) {
  .proofreading-section .main-content {
    background-attachment: scroll; }
  .proofreading-section .featureList {
    margin: 50px 0; }
    .proofreading-section .featureList .sideInfo {
      margin-top: 30px;
      margin-bottom: 20px; }
    .proofreading-section .featureList .sidePhoto img {
      position: static !important;
      margin: 0 auto 20px auto; } }



