.collaborators-section {
  padding-bottom: 50px; }
  .collaborators-section .collaborators-item {
    padding: 20px 10px;
    font-size: 1.1rem; }
    .collaborators-section .collaborators-item.right {
      text-align: right;
      padding-right: 20px; }
    .collaborators-section .collaborators-item.left {
      text-align: left;
      padding-left: 20px; }
    .collaborators-section .collaborators-item .item-prefix {
      font-variant: small-caps; }
    .collaborators-section .collaborators-item .item-name {
      text-transform: capitalize; }

@media (max-width: 767px) {
  .collaborators-item {
    text-align: center !important; } }



