.slider {
  position: relative; }

.slider *::after,
.slider *::before {
  -webkit-box-sizing: unset !important;
          box-sizing: unset !important; }

.item__image {
  max-height: 670px; }

.item__image--first img {
  height: 940px; }

.item__image--second img {
  height: 940px; }

.item__image:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(11, 25, 45, 0.4); }

.slick-slide .item__description {
  position: absolute;
  top: 25%;
  left: 8%;
