.mastFooter {
  position: relative;
  text-align: left;
  color: #abacb1;
  overflow: hidden;
  background: url("../../../assets/images/bg_parallax_05.jpg") no-repeat 50% 50%/cover fixed; }
  .mastFooter .inner {
    padding: 60px 0 30px 0;
    background: rgba(11, 25, 45, 0.85); }
    .mastFooter .inner .button:hover {
      background: #ffffff;
      color: #ffa726; }
  .mastFooter .footer--contact .contact {
    margin: 0; }
    .mastFooter .footer--contact .contact li {
      margin-bottom: 6px;
      padding-left: 35px;
      position: relative; }
    .mastFooter .footer--contact .contact i {
      position: absolute;
      left: 0;
      top: 2px; }
  .mastFooter .footer--contact .social {
    margin: 0; }
    .mastFooter .footer--contact .social a {
      color: #ffa726; }
      .mastFooter .footer--contact .social a:hover {
        color: #ffffff; }
    .mastFooter .footer--contact .social li {
      display: inline-block;
      margin-right: 10px; }
    .mastFooter .footer--contact .social i {
      font-size: 1.4rem; }
  .mastFooter .footer--rights {
    font-size: 12px; }
    .mastFooter .footer--rights .logo svg {
      width: 150px; }
    .mastFooter .footer--rights .copyright {
      margin-bottom: 0; }
  .mastFooter a {
    color: #abacb1; }
    .mastFooter a:hover {
      color: #ffa726; }
  .mastFooter h5 {
    color: #fff;
    margin: 0 0 20px 0; }
  .mastFooter .banner {
    border-bottom: 1px solid rgba(255, 255, 255, 0.2);
    padding-bottom: 45px;
    margin-bottom: 50px;
    color: #ffffff; }

@media only screen and (max-width: 992px) {
  .mastFooter h5 {
    margin: 20px 0 20px 0; }
  .mastFooter .footer--navigation {
    text-align: center; }
  .mastFooter .footer--contact {
    text-align: center; }
    .mastFooter .footer--contact .contact li {
      padding-left: 0; }
      .mastFooter .footer--contact .contact li i {
        position: initial;
        left: initial;
        margin-right: 0.5rem; }
    .mastFooter .footer--contact .social {
      margin-top: 0.5rem; }
      .mastFooter .footer--contact .social li {
        margin: 0; }
        .mastFooter .footer--contact .social li:first-child {
          margin-right: 1rem; }
        .mastFooter .footer--contact .social li i {
          margin-right: 0; } }



