/* 1.2 PAGE LAYOUT
------------------------------------------------------------ */
body, p, div, h1, h2, h3, h4, h5, h6, ul, ol, li, address, dl, dt, dd, form, textarea, input, fieldset, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, pre, code, blockquote {
  margin: 0;
  padding: 0; }

html {
  font-size: 100%; }

body {
  background: #fff;
  font: 90%/1.7 'PT Sans', Arial, sans-serif;
  color: #51556a;
  -webkit-font-smoothing: subpixel-antialiased; }

/* 1.4 TYPOGRAPHY
------------------------------------------------------------ */
p {
  margin-bottom: 1.6em; }

h1, h2, h3, h4, h5, h6 {
  margin-bottom: 20px;
  font-family: 'Montserrat', sans-serif;
  font-weight: 600;
  line-height: 1.4; }

h1 {
  font-size: 2em; }

h2 {
  font-size: 1.8em; }

h3 {
  font-size: 1.6em; }

h4 {
  font-size: 1.4em; }

h5 {
  font-size: 1.2em; }

h6 {
  font-size: 1.1em; }

.txtLite {
  color: #ffa726; }

a {
  color: #ffa726;
  text-decoration: none;
  -o-transition: color 0.2s ease-in;
  -webkit-transition: color 0.2s ease-in;
  transition: color 0.2s ease-in; }

a:hover,
a:focus {
  color: #23344b;
  text-decoration: none; }

a:focus {
  outline: none; }

.pageTitle .inner.title {
  padding: 150px 0 60px 0; }

.pageTitle .inner.title h1 {
  font-size: 3em;
  position: relative;
  color: #fff; }

.pageTitle .inner.title h1:after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 30px;
  height: 1px;
  background: #ffa726; }

.sectionTitle p {
  text-transform: uppercase;
  color: #ffa726;
  font-weight: 300;
  font-size: 12px;
  font-family: "Montserrat", sans-serif;
  margin-bottom: 25px;
  line-height: 1;
  position: relative; }

.sectionTitle .title {
  color: #23344b;
  font-size: 2.2em;
  line-height: normal;
  margin-bottom: 50px; }

.sectionTitle.base .title {
  margin-bottom: 80px; }

.sectionTitle p::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 0;
  margin-top: 15px;
  height: 1px;
  width: 20px;
  background: #a1a8b4; }

.sectionTitle.center {
  text-align: center; }

.sectionTitle.center p::after {
  left: 50%;
  margin-left: -10px; }

.sectionTitle.white .title {
  color: #fff; }

.sectionTitle.white p::after {
  background: #ccc; }

/* 2. UI PATTERNS
------------------------------------------------------------
2.1 HELPER CLASSES
------------------------------------------------------------ */
.clearfix:before,
.clearfix:after {
  content: "";
  display: table; }

.clearfix:after {
  clear: both; }

.clear {
  clear: both;
  line-height: 0;
  overflow: hidden; }

.floatRight {
  float: right; }

.floatLeft {
  float: left; }

.responsive {
  max-width: 100%; }

.fullWidth {
  width: 100%; }

.centerAlign {
  text-align: center; }

.rightAlign {
  text-align: right; }

.media,
.body {
  overflow: hidden;
  _overflow: visible;
  zoom: 1; }

.media .img {
  float: left;
  margin-right: 20px; }

.media .img img {
  display: block; }

/* columns with no gap */
.container-fluid.no-gutters {
  padding-left: 0;
  padding-right: 0; }

.row.no-gutters {
  margin-right: 0;
  margin-left: 0; }

.row.no-gutters > [class^="col-"],
.row.no-gutters > [class*="col-"] {
  padding-left: 0;
  padding-right: 0; }

/* 2.2 LISTS
------------------------------------------------------------ */
ol, ul {
  list-style-type: none; }

ul, ol {
  margin-bottom: 1em; }

.itemList li {
  margin-bottom: 8px;
  font-size: 0.9em;
  text-transform: uppercase; }

.itemList li:last-child {
  margin-bottom: 0; }

/* 2.3 BOXES & BANNERS
------------------------------------------------------------ */
.sectionTop {
  margin-top: 100px; }

.sectionBase {
  margin-bottom: 100px; }

.greyWrapper {
  padding: 100px 0;
  background: #f3f8fa; }

.highlightWrapper {
  padding: 100px 0;
  background: #ffa726;
  color: #fff; }

.highlightWrapper .button.dark:hover {
  background: #fff;
  color: #0b192d; }

.greyWrapper.banner,
.highlightWrapper.banner {
  padding: 60px 0; }

.container .banner {
  padding-left: 30px;
  padding-right: 30px; }

.banner h3 {
  margin-bottom: 5px;
  font-size: 2em; }

.banner h2,
.banner h3,
.banner h4 {
  color: inherit; }

.banner .lightText {
  font-weight: 300; }

.banner p {
  margin-bottom: 0; }

.banner .button {
  margin-top: 15px; }

.banner.large .inner {
  padding: 130px 0; }

/* 2.4 PARALLAX CONTAINERS
------------------------------------------------------------ */
.parallax {
  color: #fff;
  background: fixed no-repeat 50% 50%;
  background-size: cover;
  position: relative; }

.parallax .inner .button:hover {
  background: #fff;
  color: #ffa726; }

@media (max-width: 768px) {
  .parallax {
    background-attachment: scroll; } }

.parallax .inner {
  padding-top: 60px;
  padding-bottom: 60px;
  background: rgba(11, 25, 45, 0.8); }

.parallax h3 {
  margin-bottom: 10px; }

/* 2.6 BUTTONS
------------------------------------------------------------ */
button {
  cursor: pointer;
  overflow: visible;
  border: none;
  font-size: 1em; }

button:hover {
  cursor: pointer; }

button::-moz-focus-inner {
  padding: 0;
  border: 0; }

.button {
  position: relative;
  display: inline-block;
  padding: 8px 28px;
  background: none;
  font-weight: normal;
  text-transform: uppercase;
  font-size: .9em; }

.button:hover {
  text-decoration: none;
  -o-transition: all 0.3s ease-in;
  -webkit-transition: all 0.3s ease-in;
  transition: all 0.3s ease-in; }

.button:focus {
  outline: none; }

.button.primary {
  background: #ffa726;
  color: #fff; }

.button.primary:hover {
  background: #0b192d;
  color: #fff; }

.button.dark {
  background: #0b192d;
  color: #fff; }

.button.dark:hover {
  background: #ffa726;
  color: #fff; }

.button.large {
  padding: 10px 35px; }

/* 2.8 TABS
------------------------------------------------------------ */
.tabs ul.nav,
.tabs p:last-child {
  margin: 0; }

.tabs li.nav {
  float: left; }

.tabs li.nav i {
  line-height: normal;
  margin-right: 5px; }

.tabs li.nav a {
  padding: 10px 25px;
  float: left;
  display: block;
  margin-right: 1px;
  color: #51556a;
  background: #f3f8fa;
  -webkit-transition: all linear 0.3s;
  -o-transition: all linear 0.3s;
  transition: all linear 0.3s;
  font-weight: bold;
  text-transform: uppercase;
  font-family: "Montserrat", sans-serif;
  font-size: .85em; }

.tabs li.nav a:hover,
.tabs li.nav a.active,
.tabs li.nav a.active:hover {
  position: relative;
  color: #fff;
  background: #ffa726; }

.tabs .tabContent {
  margin: 20px 0 40px 0; }

.tabs .tabContent > p:last-child {
  margin: 0; }

.iconTabs {
  margin-bottom: 60px; }

.iconTabs .nav a {
  position: relative;
  padding: 20px 20px 20px 60px;
  color: #51556a;
  background: #f3f8fa;
  display: block;
  margin-bottom: 10px;
  -webkit-transition: all linear 0.3s;
  -o-transition: all linear 0.3s;
  transition: all linear 0.3s; }

.iconTabs .nav a .icon {
  position: absolute;
  left: 20px;
  top: 25px;
  font-size: 1.5em; }

.iconTabs .nav a h6 {
  margin-bottom: 0;
  text-transform: uppercase;
  font-size: .85em;
  -webkit-transition: all linear 0.3s;
  -o-transition: all linear 0.3s;
  transition: all linear 0.3s; }

.iconTabs .nav a p {
  margin-bottom: 0;
  font-size: .85em; }

.iconTabs .nav a.active,
.iconTabs .nav a:hover {
  background: #ffa726;
  color: #fff; }

.iconTabs .nav a.active h6,
.iconTabs .nav a:hover h6 {
  color: #fff; }

/* 2.9 ICON BOXES
------------------------------------------------------------ */
.iconBox {
  padding-left: 100px;
  margin: 30px 0;
  position: relative; }

.iconBox .icon {
  border-radius: 35px;
  width: 70px;
  height: 70px;
  line-height: 70px;
  text-align: center;
  border: 5px solid #eef4f8;
  position: absolute;
  left: 0;
  top: 0;
  -webkit-transition: all 0.2s linear;
  -o-transition: all 0.2s linear;
  transition: all 0.2s linear; }

.iconBox:hover .icon {
  background: #eef4f8; }

.iconBox h5,
.iconBox2 h5,
.iconBox3 h5 {
  text-transform: uppercase;
  font-size: 1em; }

.iconBox2 {
  margin: 30px 0;
  text-align: center; }

.iconBox2 .icon {
  margin: 0 auto 25px auto;
  border-radius: 35px;
  width: 70px;
  height: 70px;
  line-height: 75px;
  text-align: center;
  background: #ffa726;
  -webkit-transition: all 0.2s linear;
  -o-transition: all 0.2s linear;
  transition: all 0.2s linear;
  border: 4px solid #ffa726; }

.iconBox2 .icon i {
  color: #fff;
  font-size: 2.2em;
  -webkit-transition: all 0.2s linear;
  -o-transition: all 0.2s linear;
  transition: all 0.2s linear; }

.iconBox2:hover .icon {
  background: none;
  border: 4px solid #ffa726; }

.iconBox2:hover .icon i {
  color: #ffa726; }

.iconBox3 {
  padding-left: 40px;
  margin: 20px 0;
  position: relative; }

.iconBox3 p {
  margin-bottom: 0; }

.iconBox3 .icon {
  position: absolute;
  left: 0;
  top: 2px; }

/* END UI PATTERNS
------------------------------------------------------------*/



