.contact-info {
  position: absolute;
  width: 100%;
  bottom: 25px;
  z-index: 900;
  color: #ffffff;
  font-size: .9em; }
  .contact-info a {
    color: #ffffff; }
    .contact-info a:hover {
      color: #ffa726; }
  .contact-info ul {
    margin: 0; }
    .contact-info ul li {
      display: inline-block;
      margin-right: 15px; }
      .contact-info ul li:last-child {
        margin: 0; }
      .contact-info ul li i {
        margin-right: 3px; }



